@import url('https://fonts.googleapis.com/css?family=Roboto:100');

#logo {
  float: left;
}

#title {
  padding-left: 10%;
  color: #48486e;
  font-family: Roboto;
  font-weight: 900;
}

#titleHalf {
  color: #34b3d3;
  font-family: Roboto;
  font-weight: 900;
}

.nav {
  display: inline-block;
  color: black;
  font-weight: bold;
  padding: 0px 5px;
}

#navBar {
  float: right;
  margin-top: -65px;
  padding-right: 10%;
}

#accLink {
  text-decoration: none;
  color: black;
}

#about {
  padding-top: 40px;
  color: white;
  text-align: center;
  background-color: #48486e;
  font-family: Roboto;
  font-weight: 100;
  font-size: 20px;
  height: 520px;
}

#btnDetails {
  margin-top: -400px;
}

#parcelBtn {
  background-color: #48486e;
  border: 2px #34b3d3 solid;
  color: white;
  padding: 10px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
}

#parcelBtn:hover {
  background-color: #34b3d3;
  border: 2px #34b3d3 solid;
  color: white;
}

#bikeBtn {
  background-color: #48486e;
  border: 2px #34b3d3 solid;
  color: white;
  padding: 10px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
}

#bikeBtn:hover {
  background-color: #34b3d3;
  border: 2px #34b3d3 solid;
  color: white;
}

#priceBtn {
  background-color: #f6416c;
  border: 2px #f6416c solid;
  color: white;
  padding: 10px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
}

#priceBtn:hover {
  background-color: white;
  border: 2px white solid;
  color: #f6416c;
}

#procedure {
  padding-top: 40px;
  text-align: center;
  font-family: Roboto;
  font-size: 20px;
  height: 500px;
}

.steps {
  width: 25%;
  display: inline-block;
}

.underline {
  border-bottom: 3px #f6416c solid;
}

#stepDesc {
  padding-top: -10px;
}

.stepImg {
  padding-bottom: 0px;
}

.button {
  padding: 10px;
  font-family: Roboto;
}

#btn {
  margin-top: 20px;
}

#book {
  margin: 0px 5px;
  background-color: #f6416c;
  border: 2px #f6416c solid;
  color: white;
  padding: 10px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
}

#book:hover {
  background-color: white;
  border: 2px #f6416c solid;
  color: #f6416c;
}

#contact {
  margin: 0px 5px;
  background-color: white;
  border: 2px #34b3d3 solid;
  padding: 10px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
}

#contact:hover {
  background-color: #34b3d3;
  border: 2px #34b3d3 solid;
  color: white;
}

#foot {
  margin-top: 120px;
  color: white;
  text-align: center;
  background-color: #48486e;
  font-family: Roboto;
  font-weight: 100;
  font-size: 20px;
  height: 200px;
}

.footPart {
  padding-left: 10%;
  width: 12%;
  display: inline-block;
  float: left;
}

#linksList {
  margin-top: -10px;
  list-style: none;
  font-size: 14px;
}

.footLinks {
  color: white;
  text-decoration: none;
  font-family: Roboto;
  float: left;
}

.footLinks:hover {
  color: lightgray;
}

#social {
  padding-top: 40px;
}



